#game-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    #game-screen {
        border: 1px dashed red;
        width: 100%;
        height: 400px;
    }
    
    #game-inventory {
        border: 1px dashed black;
        width: 100%;
        height: 90px;
    }
}

.ascii {
    background: transparent;
    border: none;
}

.interact {
    cursor: pointer;
}